import React, { Component } from 'react';
import {
  Switch,
  Route
} from 'react-router-dom'
import App from './App';

class TopApp extends Component {
  render() {
    return (
      <div className="TopApp">
        <Switch>
          <Route path="/" exact component={App}/>
          <Route component={App} />
        </Switch>
      </div>
    );
  }
}

export default TopApp;
